.bmdb-list, .bmdb-list:before, .bmdb-list:after,
.bmdb-list *,
.bmdb-list *:before,
.bmdb-list *:after,
.bmdb-loader,
.bmdb-loader:before,
.bmdb-loader:after,
.bmdb-loader *,
.bmdb-loader *:before,
.bmdb-loader *:after {
  box-sizing: border-box; }

.bmdb-ul,
.bmdb-li {
  margin: 0;
  padding: 0;
  list-style: none; }

.bmdb-ul {
  margin-left: -2%;
  margin-right: -2%;
  margin-bottom: 0;
  padding: 0;
  background: none;
  line-height: 100%; }
  .bmdb-ul:before, .bmdb-ul:after {
    content: "";
    display: table; }
  .bmdb-ul:after {
    clear: both; }

.bmdb-li {
  list-style: none;
  float: left;
  width: 16%;
  display: block;
  font-size: 13px;
  padding: 0;
  margin-left: 2%;
  margin-right: 2%;
  margin-bottom: 20px; }

.bmdb-link {
  color: #444; }
  .bmdb-link, .bmdb-link:focus, .bmdb-link:hover {
    text-decoration: none;
    box-shadow: none; }

.bmdb-cover {
  position: relative;
  min-height: 87px;
  overflow: hidden;
  background: #fafafa; }
  .bmdb-cover:before {
    content: "";
    display: block;
    padding-top: 143%; }

.bmdb-cover-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: auto;
  border-radius: 0;
  box-shadow: none; }

.bmdb-info {
  height: 45px;
  overflow: hidden; }

.bmdb-title {
  display: -webkit-box;
  font-size: 13px;
  padding: 10px 0 0;
  color: #494949;
  line-height: 15px;
  overflow: hidden;
  text-overflow: ellipsis;
  font-weight: 400;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical; }

.bmdb-rank {
  height: 16px;
  font-size: 12px;
  padding-top: 3px; }

.bmdb-stars {
  margin-right: 5px; }

.bmdb-star {
  display: inline-block;
  margin-right: 1px;
  width: 10px;
  height: 10px;
  background-color: transparent;
  background-repeat: no-repeat;
  background-size: 10px 10px; }

.bmdb-star-full {
  background-image: url("data:image/png;base64,"); }

.bmdb-star-gray {
  background-image: url("data:image/png;base64,"); }

.bmdb-category {
  margin-bottom: 30px; }
  .bmdb-category ul,
  .bmdb-category li {
    margin: 0;
    padding: 0;
    list-style: none; }
  .bmdb-category li {
    display: inline-block;
    margin-right: 10px; }
    .bmdb-category li a {
      color: #888;
      text-decoration: none; }
      .bmdb-category li a:hover {
        color: #333; }
      .bmdb-category li a.bmdb-category-active {
        color: #157efb; }

.bmdb-loader {
  color: #999;
  text-align: center;
  padding: 10px 0; }
  .bmdb-loader .bmdb-cover,
  .bmdb-loader .bmdb-title,
  .bmdb-loader .bmdb-rank {
    background: -webkit-gradient(linear, left top, right top, color-stop(25%, #f2f2f2), color-stop(37%, #e6e6e6), color-stop(63%, #f2f2f2));
    background: linear-gradient(90deg, #f2f2f2 25%, #e6e6e6 37%, #f2f2f2 63%);
    background-size: 400% 100%;
    animation: skeleton-loading 1.4s ease infinite; }
  .bmdb-loader .bmdb-title {
    width: 55%;
    height: 15px;
    padding-top: 0;
    margin-top: 10px; }
  .bmdb-loader .bmdb-rank {
    width: 70%;
    height: 13px;
    padding-top: 0;
    margin-top: 3px; }

@keyframes skeleton-loading {
  0% {
    background-position: 100% 50%; }
  100% {
    background-position: 0 50%; } }

@media screen and (max-width: 768px) {
  .bmdb-li {
    width: 29.33%; } }
